import Vue from 'vue';
import Router from 'vue-router';
import Home from './src/components/Home';
import ArticleDetails from './src/components/ArticleDetails';

Vue.use(Router);

export default new Router({
  mode: 'history',

  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/articleDetails/:id',
      name: 'ArticleDetails',
      component: ArticleDetails
    }
  ]
})